import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';

import 'rxjs/add/observable/throw';
/*
  Generated class for the ProfileServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MasterServiceProvider {

    // private baseUrl: string = "http://clinic-api.apk";
    private baseUrl: string = "http://clinic-apiv2.apk";
    private agamaUrl: string = `${this.baseUrl}/v1/agama`;
    private tempatUrl: string = `${this.baseUrl}/v1/tempat_lahir`;
    private titleUrl: string = `${this.baseUrl}/v1/title`;
    private darahUrl: string = `${this.baseUrl}/v1/gol_darah`;
    private identitasUrl: string = `${this.baseUrl}/v1/identitas`;
    private pendidikanUrl: string = `${this.baseUrl}/v1/pendidikan`;
    private pekerjaanUrl: string = `${this.baseUrl}/v1/pekerjaan`;
    private status_kawinUrl: string = `${this.baseUrl}/v1/status_kawin`;
    private unit_usahaUrl: string = `${this.baseUrl}/v1/unit_usaha`;
    private dokterUrl: string = `${this.baseUrl}/v1/dokter`;
    private reminderUrl: string = `${this.baseUrl}/v1/reminder`;

    private layananUrl2: string = `${this.baseUrl}/v1/list_layanan_by_unit_usaha`;
    private dokterUrl2: string = `${this.baseUrl}/v1/list_dokter_by_layanan`;

    //without auth
    private noAuth: string = `${this.baseUrl}/v1/without_auth`;
    private titleUrl2: string = `${this.noAuth}/title`;
    private identitasUrl2: string = `${this.noAuth}/identitas`;
    private tempatUrl2: string = `${this.noAuth}/tempat_lahir`;

    token: any;

    constructor(public http: Http) {
        console.log('Master');
    }

    get_list_tempat_lahir(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.tempatUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_unit_usaha(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.unit_usahaUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_agama(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.agamaUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }
    
    get_list_golDarah(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.darahUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_status_kawin(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.status_kawinUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_pendidikan(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.pendidikanUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_pekerjaan(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.pekerjaanUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_title(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });

        return this.http.get(this.titleUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }
    
    get_list_identitas(token: string){
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });

        return this.http.get(this.identitasUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_title_2(){

        return this.http.get(this.titleUrl2)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_identitas_2(){

        return this.http.get(this.identitasUrl2)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_tempat_2(){

        return this.http.get(this.tempatUrl2)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_list_dokter(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(this.dokterUrl, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_layanan_by_unit_usaha(token: string, id: number) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(`${this.layananUrl2}/${id}`, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    get_dokter_by_layanan(token: string, id: number) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(`${this.dokterUrl2}/${id}`, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }
    get_reminder(token: string) {
        const headers = this.headers(token);

        const options = new RequestOptions({ headers: headers });


        return this.http.get(`${this.reminderUrl}`, options)
            .do(this.logData)
            .map(this.extractData)
            .do(this.logData)
            .catch(this.handleError)
    }

    headers(token: string) {
        this.token = `Bearer ${token}`;

        const headers = new Headers();
        headers.append('Content-Type', 'application/json');
        headers.append('Authorization', this.token);

        return headers;

    }

    private handleError(error: Response | any) {
        return Observable.throw(error)
    }

    private extractData(response: Response) {
        return response.json();
    }

    private logData(response: Response) {
        console.log(response)
    }

}



