import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';

import 'rxjs/add/observable/throw';

@Injectable()
export class NewsServiceProvider {

  // private baseUrl: string = "http://clinic-api.apk";
  private baseUrl: string = "http://clinic-apiv2.apk";
  private newsUrl: string = `${this.baseUrl}/v1/news`;

  constructor(
    private http: Http,
  ) {

  }

  get_all_news(page: number) {

    return this.http.get(`${this.newsUrl}?=${page}`)
      .do(this.logData)
      .map(this.extractData)
      .do(this.logData)
      .catch(this.handleError)
  }

  get_by_uid(uid: string) {

    return this.http.get(`${this.newsUrl}/${uid}`)
      .do(this.logData)
      .map(this.extractData)
      .do(this.logData)
      .catch(this.handleError)
  }

  private handleError(error: Response | any) {
    return Observable.throw(error)
  }

  private extractData(response: Response) {
    return response.json();
  }

  private logData(response: Response) {
    console.log(response)
  }


}



