import { Component, Input, ViewChild } from '@angular/core';
import { Content, ItemSliding } from 'ionic-angular';

@Component({
    selector: 'list-reservasi',
    templateUrl: 'list-reservasi.html'
})
export class ListReservasiComponent {
    @Input() data: any;
    @ViewChild(Content)
    content: Content;

    constructor() { }

    undo = (slidingItem: ItemSliding) => {
        slidingItem.close();
    }

    delete = (item: any): void => {
        let index = this.data.indexOf(item);
        if (index > -1) {
            this.data.splice(index, 1);
        }
    }
}



