import { Http, Response, RequestOptions, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as moment from 'moment';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';

import 'rxjs/add/observable/throw';

/*
  Generated class for the ReservasiServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class ReservasiServiceProvider {

  // private baseUrl: string = "http://clinic-api.apk";
  private baseUrl: string = "http://clinic-apiv2.apk";
  private ionCalendarUrl: string = `${this.baseUrl}/v1/reservasi/ionic_calendar`;
  private reservasiByDateUrl: string = `${this.baseUrl}/v1/reservasi/date`;
  private baseReservasiUrl: string = `${this.baseUrl}/v1/reservasi`;
  private ongoingBaseUrl: string = `${this.baseUrl}/v1/reservasi/ongoing`;
  private reservasiUrl: string;
  private timeUrl: string = `${this.baseUrl}/v1/reservasi/time`;
  token: any;

  constructor(public http: Http) {
    console.log('Hello ReservasiServiceProvider Provider');
  }


  saveReservasi(reservasi: any, token: string, uid: string) {
    
    console.log('d',reservasi.tanggal)
    let tanggal = moment(reservasi.tanggal,'DD/MM/YYYY').format('YYYY-MM-DD');
    let t = moment(reservasi.waktu,'HH:mm:ss').format('HH:mm');

    let waktu = reservasi.waktu ? t : '';
    console.log('waktu', t);
    let formData: FormData = new FormData();
    formData.append('tanggal', tanggal);
    formData.append('waktu', waktu);
    formData.append('unit_usaha_id', reservasi.unit_usaha_id);
    formData.append('layanan_id', reservasi.layanan_id);
    formData.append('dokter_id', reservasi.dokter_id);
    formData.append('reminder_1',reservasi.reminder_1_id);
    formData.append('reminder_2', reservasi.reminder_2_id);

    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });
    
    console.log(formData);

    if(uid == null){
    	this.reservasiUrl = `${this.baseReservasiUrl}`;
    }else{
    	this.reservasiUrl = `${this.baseReservasiUrl}/${uid}`;
    }
    return this.http.post(this.reservasiUrl, formData, options)
      .do(this.logData)
      .map(this.extractData)
      .do(this.logData)
      .catch(this.handleError)

  }

  reservasiCancel(token: string, uid: string, unit_usaha_id: any){
    let formData: FormData = new FormData();
    formData.append('unit_usaha_id', unit_usaha_id);

    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });

    let url = `${this.baseReservasiUrl}/${uid}/cancel`;
    return this.http.post(url, formData, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  ionCalendar(token: string) {
    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });


    return this.http.get(this.ionCalendarUrl, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  get_list_byDate(token: string,tanggal: string) {
    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });

    let url = `${this.reservasiByDateUrl}?tanggal=${tanggal}`
    return this.http.get(url, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  get_reserv_by_uid(token: string,uid: string) {
    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });

    let url = `${this.baseReservasiUrl}/${uid}`
    return this.http.get(url, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  get_history_reservasi(token: string,from: any, to: any, page: number){
    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });

    if(from && to){
      let dari = moment(from).format('YYYY-MM-DD');
      let sampai = moment(to).format('YYYY-MM-DD');
      var historyReservasi = `${this.baseReservasiUrl}?tanggal_dari=${dari}&tanggal_sampai=${sampai}&page=${page}`;
    }else{
      var historyReservasi = `${this.baseReservasiUrl}?page=${page}`;
    }

    return this.http.get(historyReservasi, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  get_list_on_going(token: string,page: number){
    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });

    let date = new Date();
    let month = date.getMonth() + 1;
    let year = date.getFullYear();

    let ongoingUrl = `${this.ongoingBaseUrl}?tahun=${year}&bulan=${month}&page=${page}`;

    return this.http.get(ongoingUrl, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  get_list_time(token: string,date: string){
    const headers = this.headers(token);

    const options = new RequestOptions({ headers: headers });

    return this.http.get(`${this.timeUrl}?date=${date}`, options)
        .do(this.logData)
        .map(this.extractData)
        .do(this.logData)
        .catch(this.handleError)
  }

  private headers(token: string){
    this.token = `Bearer ${token}`;

    const headers = new Headers();
    // headers.append('Content-Type', 'application/json');
    headers.append('Authorization', this.token);
    
    return headers;

  }

  private handleError(error: Response | any) {
    return Observable.throw(error)
  }

  private extractData(response: Response) {
    return response.json();
  }

  private logData(response: Response) {
    console.log(response)
  }

}



