import { NgModule, ErrorHandler, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { HttpModule } from '@angular/http';
import { MyApp } from './app.component';

import { ComponentsModule } from '../components/components.module';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireAuthModule } from 'angularfire2/auth';

import { AppSettings } from '../services/app-settings'
import { ToastService } from '../services/toast-service'
import { LoadingService } from '../services/loading-service'

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { GoogleAnalytics } from '@ionic-native/google-analytics';
import { MenuServiceProvider } from '../providers/menu-service/menu-service';
import { ToastServiceProvider } from '../providers/toast-service/toast-service';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { LoadingServiceProvider } from '../providers/loading-service/loading-service';
import { ProfileServiceProvider } from '../providers/profile-service/profile-service';
import { SelectSearchableModule } from 'ionic-select-searchable';
import { MasterServiceProvider } from '../providers/master-service/master-service';
import { MasterWilayahServiceProvider } from '../providers/master-service/master-wilayah-services';
import { ModalServiceProvider } from '../providers/modal-service/modal-service';
import { AlertProvider } from '../providers/alert/alert';
import { AlertServiceProvider } from '../providers/alert-service/alert-service';
import { IonicStorageModule } from '@ionic/storage';
import { LocalStorageServiceProvider } from '../providers/local-storage-service/local-storage-service';
import { ReservasiServiceProvider } from '../providers/reservasi-service/reservasi-service';
import { ConfigUrl } from '../providers/ConfigUrl';
import { BrMaskerModule } from 'brmasker-ionic-3';

//native
import { File } from '@ionic-native/file';
import { Transfer } from '@ionic-native/transfer';
import { FilePath } from '@ionic-native/file-path';
import { Camera } from '@ionic-native/camera';
import { PhotoViewer } from '@ionic-native/photo-viewer';
import { Network } from '@ionic-native/network';
import { RegisterServiceProvider } from '../providers/register-service/register-service';
import { ScreenOrientation } from '@ionic-native/screen-orientation';

import { SuperTabsModule } from 'ionic2-super-tabs';
import { NewsProvider } from '../providers/news/news';
import { NewsServiceProvider } from '../providers/news-service/news-service';
import { FlashProvider } from '../providers/flash/flash';


@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {statusbarPadding: true}),
    AngularFireModule.initializeApp(AppSettings.FIREBASE_CONFIG),
    AngularFireDatabaseModule, AngularFireAuthModule, AngularFirestoreModule,
    SelectSearchableModule,
    IonicStorageModule.forRoot(),
    BrMaskerModule,
    SuperTabsModule.forRoot(),
    ComponentsModule,
    BrowserAnimationsModule,
  ],
  bootstrap: [IonicApp],
  entryComponents: [MyApp],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  providers: [
    BarcodeScanner, StatusBar, SplashScreen,
    ToastService, LoadingService, GoogleAnalytics,
    //native
    File,
    Transfer,
    Camera,
    FilePath,
    PhotoViewer,
    Network,
    ScreenOrientation,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    MenuServiceProvider,
    ToastServiceProvider,
    ProfileServiceProvider,
    AuthServiceProvider,
    LoadingService,
    LoadingServiceProvider,
    MasterServiceProvider,
    MasterWilayahServiceProvider,
    ModalServiceProvider,
    ModalServiceProvider,
    AlertServiceProvider,
    LocalStorageServiceProvider,
    ReservasiServiceProvider,
    ConfigUrl,
    RegisterServiceProvider,
    NewsServiceProvider,
    FlashProvider,
  ]
})
export class AppModule {
}



