import { Http, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Storage } from '@ionic/storage';

import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';

import 'rxjs/add/observable/throw';

import { User } from '../../models/user.interface';
import { NavController, Events, App } from 'ionic-angular';
import { ModalServiceProvider } from '../modal-service/modal-service';

@Injectable()
export class AuthServiceProvider {

  // private baseUrl: string = "http://clinic-api.apk";
  private baseUrl: string = "http://clinic-apiv2.apk";
  private authUrl: string = `${this.baseUrl}/v1/auth/login`;
  private navCtrl: NavController;
  private page: string;
  private dumpUser: any;

  constructor(
    private http: Http,
    private events: Events,
    private app: App,
    private modal: ModalServiceProvider,
    private storage: Storage,
  ) {
    console.log('Auth Providers');
    this.navCtrl = app.getActiveNav();
  }

  //send user
  AuthUser(user: User){
    this.dumpUser = user;
    let formData:FormData = new FormData();
    formData.append('username', user.username.replace(/\D/g, ''));
    formData.append('password', user.password);
    
    return this.http.post(this.authUrl, formData)
      .do(this.logData)
      .map(this.extractData)
      .do(this.logData)
      .catch(this.handleError)
  }

  //middleware
  AuthCheck(token: string){
    this.storage.get('user').then((user) => {
      if (token == undefined && user ==  undefined) {
        this.events.publish('user:redirect', null, null);
      }
    })
  }

  setUser(token, data) {
    console.log('User Loged!');
    //storage set
    this.storage.set('auth:status', true);
    this.storage.set('token', token);
    this.storage.set('user:profile', data);
    //events set
    this.events.publish('user:profile', data);
    this.events.publish('auth:status', true);
  }

  logOut(){
    console.log('User Not Loged!')
    //storage set
    this.storage.set('auth:status', false);
    this.storage.set('token', null);
    this.storage.set('user:profile', null);
    //events set
    this.events.publish('user:profile',null);
    this.events.publish('auth:status', false);
  }

  autoLogin(user: User){
    this.storage.set('user', user);
    
    let formData:FormData = new FormData();
    formData.append('username', user.username);
    formData.append('password', user.password);
    
    return this.http.post(this.authUrl, formData)
      .do(this.logData)
      .map(this.extractData)
      .do(this.logData)
      .catch(this.handleError)
      .subscribe(data => this.setUser(data.token, data.data), error => this.logOut())
  }

  private handleError(error: Response | any) {
    return Observable.throw(error)
  }

  private extractData(response: Response) {
    return response.json();
  }

  private logData(response: Response) {
    console.log(response)
  }
  
  
}



