import { Profile } from "../models/profile.interface";
import { Component, ViewChild } from '@angular/core';
import { Network } from '@ionic-native/network';
import { Storage } from '@ionic/storage';

import { Platform, MenuController, Nav, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { MenuService } from '../services/menu-service';
import { NetworkServiceProvider } from '../providers/network-service/network-service';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { ScreenOrientation } from '@ionic-native/screen-orientation';



@Component({
  templateUrl: 'app.html',
  providers: [MenuService]
})

export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage = "HomePage";
  pages_1: any;
  pages_2: any;
  pages_3: any;
  params:any;
  leftMenuTitle: string;
  private token: string;

  public auth: boolean = false;
  public profile: Profile;
  public foto: any;

  constructor(
    public platform: Platform,
    public menu: MenuController,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private menuService: MenuService,
    private events: Events,
    private storage: Storage,
    private authService: AuthServiceProvider,
    private screenOrientation: ScreenOrientation
  ) {

    this.initializeApp();
    this.userAuth();
    this.initUser();
    //Change Color StatusBar
    this.statusBar.backgroundColorByName("white");
    //page 
    this.pages_1 = this.menuService.getGuestMenu();
    this.pages_2 = this.menuService.getClinicUserMenu();
    this.pages_3 = this.menuService.getAuthMenu();
    
  }

  initializeApp() {
    if(this.platform.is('android') && this.platform.is('ios')){
      this.screenOrientation.lock(this.screenOrientation.ORIENTATIONS.PORTRAIT);
    }
    
    this.platform.ready().then(() => {
      this.statusBar.overlaysWebView(true);
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  private initUser() {
    this.events.subscribe('user:profile', (data) => {
      if (data) {
        let timeStamp = Math.floor(Date.now());
        this.profile = data;
        this.foto = `${this.profile.foto}?ts=${timeStamp}`;
      }
    });

    this.events.subscribe('foto:update', (timeStamp) => {
      this.foto = `${this.profile.foto}?ts=${timeStamp}`;
      console.log('foto', this.foto)
    });
    
  }
  
  private userAuth(){
    this.events.subscribe('user:redirect', () => {
      console.log('Redirect to Login Page');
      this.nav.setRoot('LoginPage');
    });

    this.events.subscribe('auth:status', (status) => {
      console.log('auth:status', status)
      if (status) {
        this.auth = true;
      } else {
        this.nav.setRoot('HomePage');
        this.auth = false;
      }
    });

    this.storage.get('user').then((user) => {
      if (user != undefined || user != null) {
        console.log('User Exist : ', user)
        this.authService.autoLogin(user)
        this.storage.get('auth:status').then((condition) => {
          console.log('Login status : ', condition)
          this.auth = condition;
        })
      }
    });
    
  }


  public openPage(page) {
    this.menu.open();
    this.nav.setRoot(page.page, {
      page: page,
    });
  }

  public toLogin() {
    this.nav.setRoot('LoginPage')
  }

  public toSerial() {
    this.nav.setRoot('SerialNumberPage');
  }

  public logOut() {
    this.authService.logOut();
  }

  public referencesTo() {
    if(this.auth){
      if (this.profile.pasien_id == -1){
        this.toSerial();
      }else{
        //point page
      }
    }else{
      this.toLogin();
    }
  }

}



